.result {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: rgba(255, 255, 255, 0.89);
    align-items: center;
    min-width: 40%;
}

.title {
    margin: 5% 5% 5% 5%;
    text-align: center;
    /* font-size: 1.8vw; */
}

.image {
    border-radius: 10%;
    /* max-width: 30vw; */
    /* max-height: 25vh; */
    max-width: 300px;
    max-height: 250px;
}

/* .calories {
    font-size: .8vw;
} */

.ingredients {
    padding: 20px 30px 20px 30px;
    list-style: none;
    margin: auto;
    /* font-size: .9vw; */
}

a {
    text-decoration: none;
    color: black;
    margin: 0px 20px 20px 20px;
    /* font-size: .9vw; */
}

.sourceLabel {
    font-weight: bold;
}

.sourceLink{
    text-decoration: underline;
}