.info {
    float: right;
    margin-top: .4vh;
    margin-right: 1vh;
    margin-bottom: 2vh;
}

.info button {
    border-radius: 50%;
    background-color: whitesmoke;
    border: whitesmoke;
    cursor: pointer;
}

.tooltiptext {
    visibility: hidden;
    width: 180px;
    background-color: rgb(48, 47, 47);
    color: whitesmoke;
    text-align: left;
    border-radius: 6px;
    padding: 5px 10px;

    /* Position the tooltip */
    position: absolute;
    z-index: 1;
    top: 2vh;
    right: 2vh;
}

.info button:hover .tooltiptext {
    visibility: visible;
}