.App {
    min-height: 100vh;
    background-image: linear-gradient(to right, #ffecd2 0%, #fcb69f 100%);
}

.search-form {
    min-height: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-bar {
    width: 50%;
    border: none;
    padding: 10px;
}

.search-button {
    background: lightcoral;
    border: none;
    padding: 10px 20px;
    color: white;
}


.search-tag {
    list-style: none;
    padding: 20px;
}

.search-tag label{
    display: inline-block;
    background-color: rgba(255, 255, 255, .9);
    border: 2px solid rgba(139, 139, 139, .3);
    color: #adadad;
    border-radius: 25px;
    white-space: nowrap;
    margin: 3px 0px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
    transition: all .2s;
    padding: 4px 10px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
}

.search-tag label::before {
    /* font-family: "Font Awesome 5 Free"; */
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    font-weight: 900;
    font-size: 14px;
    padding: 2px 6px 2px 2px;
    /* content: "\f067"; */
    content: "\271A";
    transition: transform .3s ease-in-out;
}

.search-tag input[type="checkbox"]:checked + label::before {
    /* content: "\f00c"; */
    content: "\2714";
    transform: rotate(-360deg);
    transition: transform .3s ease-in-out;
}

.search-tag input[type="checkbox"]:checked + label {
    border: 2px solid #2ee20e;
    background-color: rgb(14, 151, 14);
    color: #fff;
    transition: all .2s;
}

.search-tag input[type="checkbox"] {
    display: absolute;
    position: absolute;
    opacity: 0;
}
.search-tag input[type="checkbox"]:focus + label {
    border: 2px solid #e9a1ff;
}